/**
 * 
 * @param {CanvasRenderingContext2D} ctx 
 * @param {Number} x xfabric
 * @param {Number} y yfabric
 * @param {Number} tolerance 
 * @return {boolean} truefalse
 */
export const isTransparent = (
  ctx: CanvasRenderingContext2D,
  x: number,
  y: number,
  tolerance: number,
): boolean => {
  tolerance = Math.round(tolerance); // 
  const size = tolerance * 2 + 1; // 
  const { data } = ctx.getImageData(x - tolerance, y - tolerance, size, size); // 

  //  -  > 1 4
  for (let i = 3; i < data.length; i += 4) {
    const alphaChannel = data[i]; // alpha
    if (alphaChannel > 0) {
      return false; // false
    }
  }
  return true; // true
};
